<script setup>
import { ref, defineProps } from "vue";

import RangeBar from "@/components/layout/RangeBar";

const props = defineProps({
    close: Function,
});

const facilities = ref([
    { id: "1", tit: "" },
    { id: "2", tit: "" },
    { id: "3", tit: "" },
    { id: "4", tit: "" },
    { id: "5", tit: "Wi-fi" },
    { id: "6", tit: "" },
    { id: "7", tit: "" },
    { id: "8", tit: "" },
    { id: "9", tit: "" },
    { id: "10", tit: "" },
]);

const machine = ref([{ title: "" }, { title: "" }, { title: "" }, { title: "" }, { title: "" }, { title: "" }]);

const machineTag = ref([
    ["", "", "", " "],
    ["", " ", " ", "", "  ", "  ", "  "],
    [" ", " ", " ", "  "],
    [" ", " ", "  ", "", ""],
    ["  ", " ", "  "],
    ["", "", ""],
]);

const dateRangeMin = ref(0);
const dateRangeMax = ref(12);
const priceRangeMin = ref(40000);
const priceRangeMax = ref(120000);
</script>

<template>
    <header class="at-header">
        <div class="at-header-inner">
            <div class="back" @click="props.close"><img src="@/assets/image/back.png" alt="" /></div>
            <h1 class="f-16-400"></h1>
        </div>
    </header>
    <div class="filter_container main-layout">
        <div class="filter_container_inner mob-inner">
            <!--  -->
            <div class="ticket-date">
                <div class="ticket-date-tit">
                    <p class="f-14-700"></p>
                    <ul>
                        <li class="active"> </li>
                        <li>1 </li>
                    </ul>
                </div>
                <div class="ticket-date-adjust">
                    <p>
                        <span>{{ dateRangeMin }}</span>~<span>{{ dateRangeMax }}</span>
                    </p>
                    <RangeBar
                        :defaultMinValue="0"
                        :defaultMaxValue="12"
                        :defaultRangeL="0"
                        :defaultRangeR="12"
                        @changeRange="
                            dateRangeMax = $event.max;
                            dateRangeMin = $event.min;
                            console.log($event);
                        "
                    />
                </div>
            </div>
            <!--  -->
            <div class="ticket-expense">
                <div class="ticket-expense-tit f-14-700"><p>(1 )</p></div>
                <div class="ticket-expense-adjust">
                    <p>
                        <span>{{ priceRangeMin }}</span>~<span>{{ priceRangeMax }}</span>
                    </p>
                    <RangeBar
                        :defaultMinValue="0"
                        :defaultMaxValue="12"
                        :defaultRangeL="4"
                        :defaultRangeR="12"
                        @changeRange="
                            priceRangeMin = $event.min * 10000;
                            priceRangeMax = $event.max * 10000;
                            console.log($event);
                        "
                    />
                </div>
            </div>
            <!--   -->
            <div class="place-type">
                <div class="place-type-tit f-14-700"> </div>
                <ul>
                    <li>24</li>
                    <li> </li>
                    <li> </li>
                    <li></li>
                </ul>
            </div>
            <!--  -->
            <div class="etc-opt">
                <div class="etc-opt-tit f-14-700"></div>
                <ul>
                    <li>P.T</li>
                    <li> P.T</li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li>G.X</li>
                </ul>
            </div>
            <!--  -->
            <div class="facilities">
                <div class="facilities-tit f-14-700"></div>
                <ul>
                    <li v-for="fac in facilities" :key="fac.id">
                        <span :class="'facilities-icon facilities-icon-' + fac.id"></span>
                        <p>{{ fac.tit }}</p>
                    </li>
                </ul>
            </div>
            <!--  -->
            <div class="machine">
                <div class="machine-tit f-14-700 place-each-tit"> </div>
                <div class="machine-list" v-for="(machineItem, idx) in machine" :key="idx">
                    <span class="machine-tit f-14-400">{{ machineItem.title }}</span>
                    <ul>
                        <li v-for="(data, _idx) in machineTag[idx]" :key="_idx">{{ data }}</li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="filter-bott-btn">
            <button class="f-16-700"></button>
            <button class="f-16-700"> 200 </button>
        </div>
    </div>
</template>
